.mdl-card {
  height: auto;
  display: flex;
  flex-direction: column;
  background-color: #fcfcfc;
  width: 100%;

  &> * {
    height: auto;
  }

  h4 {
    margin-top: 0;
    margin-bottom: 20px;
  }

  .mdl-card__supporting-text {
    margin: 20px;
    flex-grow: 1;
    padding: 0;
    color: inherit;
    width: calc(100% - 80px);

    p {
      font-size: 15px;
      font-family: Roboto, 'Helvetica Neue', Arial, Helvetica, sans-serif;
      font-weight: 300;
    }

    &+ .mdl-card__actions {
      border-top: 1px solid rgba(0, 0, 0, 0.12);
    }

    &> h3:first-child {
      margin-bottom: 24px;
    }
  }

  .mdl-card__actions {
    margin: 0;
    padding: 4px 0;
    color: inherit;

    a {
      color: red;
      margin: 0;
      font-family: 'Bitter', 'Roboto', sans-serif;
      font-weight: 700;
      text-transform: none;
      letter-spacing: 0.03em;

      &:hover, &:active {
        color: inherit;
        background-color: transparent;
      }
    }
  }
}
